import {IUser} from "./UsersSlice";
import {createSlice} from "@reduxjs/toolkit";

export interface IRoom {
    title: string
    userLimit: number
    private: boolean
    password: string | undefined
    uuid: string
    photo: string
    admin: IUser[]
    users: IUser[]
}

interface IInitialState {
    rooms: IRoom[] | undefined
}

const initialState: IInitialState = {
    rooms: []
}


export const roomsSlice = createSlice({
    name: 'rooms',
    initialState,
    reducers: {
        add(state, actions) {
            state.rooms = actions.payload
        },
        delete(state, actions) {
            if (state.rooms !== undefined)  {
                let newArray = state.rooms.slice();
                newArray.filter(opt => {
                    if (opt.uuid !== actions.payload) return opt
                })
            }
        }
    }
})


export default roomsSlice.reducer