import React, {FC, useEffect, useState} from "react";
import {useNavigate} from "react-router";
import {v4} from "uuid";
import {
    Avatar,
    Button, Checkbox, Container, Dialog, DialogActions, DialogContent, DialogTitle,
    Divider, FormControlLabel, Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText, Paper, Stack,
    TextField,
    Typography
} from "@mui/material";
import {useAppDispatch, useAppSelector} from "../store/hooks/redux";
import {IRoom, roomsSlice} from "../store/reducers/RoomSlice";
import socket from "../socket";
import ACTIONS from '../socket/actions'
import {ClearStore} from "../global-elements/functions/ClearStore";

//@ts-ignore
import johnsonImg from "../static/images/avatar_group.jpg"


const CreateRoom: FC = () => {
    useEffect(() => {
        socket.on(ACTIONS.SHARE_ROOMS, ({rooms = []} = {}) => {
            console.log(rooms)
        });
    }, []);

    const navigate = useNavigate()

    const dispatch = useAppDispatch()

    const {users, selectUser} = useAppSelector(state => state.usersReducer)
    const {rooms} = useAppSelector(state => state.roomsReducer)

    const [passRoom, setPassRoom] = useState<string | undefined>(undefined)
    const [dataRoom, setDataRoom] = useState<IRoom | undefined>()

    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    const handleAdd = () => {

        const url = v4()

        if (rooms !== undefined) {
            dispatch(ClearStore(roomsSlice.actions.add([
                ...rooms,
                {
                    title: dataRoom?.title,
                    // @ts-ignore
                    userLimit: dataRoom?.userLimit < 1 ? 1 : dataRoom?.userLimit,
                    private: dataRoom?.private,
                    password: dataRoom?.password,
                    uuid: url,
                    photo: 'src/pages/avatar_group.jpg',
                    admin: [selectUser],
                    users: [selectUser]
                }
            ])))
        } else {
            dispatch(ClearStore(roomsSlice.actions.add([
                {
                    title: dataRoom?.title,
                    // @ts-ignore
                    userLimit: dataRoom?.userLimit < 1 ? 1 : dataRoom?.userLimit,
                    private: dataRoom?.private,
                    password: dataRoom?.password,
                    uuid: url,
                    photo: 'src/pages/avatar_group.jpg',
                    admin: [selectUser],
                    users: [selectUser]
                }
            ])))
        }


        setOpen(false)
    };

    return (
        <React.Fragment>
            <Container>
                <Grid container justifyContent={'center'}>


                </Grid>
            </Container>
        </React.Fragment>
    )
}

export default CreateRoom