import React, {FC, useEffect, useState} from "react";
import useWebRTC, {LOCAL_VIDEO} from "../hooks/useWebRTC";
import {useNavigate, useParams} from "react-router";
import {Alert, Box, CircularProgress, Grid, Stack} from "@mui/material";
import {useAppDispatch, useAppSelector} from "../store/hooks/redux";
import {usersSlice} from "../store/reducers/UsersSlice";


const AuthTemplate: FC = () => {

    const dispatch = useAppDispatch()

    const {users} = useAppSelector(state => state.usersReducer)

    const {id: roomID} = useParams();
    const {clients, provideMediaRef} = useWebRTC(roomID);

    const [textInfo, setTextInfo] = useState<{
        text: string
        confirmation: boolean
    }>({
        text: '       ,  ',
        confirmation: false
    });

    const navigate = useNavigate()

    useEffect(() => {
        const f = () => {
            setTextInfo({
                text: '        Telegram',
                confirmation: true
            })
            navigate(`/profile/${users[1].uuid}`)
        }
        setTimeout(f, 5000)

        dispatch(usersSlice.actions.select(users[1]))
    }, [])

    return (
        <React.Fragment>
            <Grid container justifyContent={'center'} sx={{
                height: '100vh',
            }}>
                <Grid item xs={12} sm={12} md={12} lg={12} xl={12}
                      sx={{
                          alignItems: 'center',
                          display: 'contents',
                      }}
                >
                    <Stack
                        direction="column"
                        justifyContent="center"
                        alignItems="center"
                        spacing={4}
                        sx={{
                            justifyContent: 'center',
                            textAlign: 'center',
                        }}
                    >
                        {clients.map((clientID: any, index: any) => {
                            if (clientID === LOCAL_VIDEO) {
                                return (
                                    <Stack
                                        spacing={2}
                                        key={clientID}
                                        sx={{
                                            width: '400px',
                                            height: '400px',
                                            padding: '20px',
                                            display: textInfo.confirmation ? 'none' : 'block'
                                        }}
                                    >
                                        <div id={clientID} style={{
                                            width: '400px',
                                            height: '400px',
                                            borderRadius: '50%',
                                            overflow: 'hidden',
                                        }}>
                                            <video
                                                width='100%'
                                                height='100%'
                                                style={{
                                                    objectFit: 'cover'
                                                }}
                                                ref={instance => {
                                                    provideMediaRef(clientID, instance);
                                                }}
                                                autoPlay
                                                playsInline
                                                muted={clientID === LOCAL_VIDEO}
                                            />
                                        </div>
                                    </Stack>
                                );
                            }
                        })}

                        <Alert variant="outlined" severity={textInfo.confirmation ? "info" : "warning"}>
                            <Stack
                                direction="column"
                                justifyContent="center"
                                alignItems="center"
                                spacing={2}
                            >
                                <Box sx={{display: 'flex'}}>
                                    <CircularProgress color={textInfo.confirmation ? "info" : "warning"}/>
                                </Box>
                                <p>{textInfo.text}</p>
                            </Stack>
                        </Alert>
                    </Stack>
                </Grid>
            </Grid>
        </React.Fragment>
    )
}

export default AuthTemplate