import React, {FC} from "react";
import {Container, Grid, Paper, Stack} from "@mui/material";
import useWebRTC, {LOCAL_VIDEO} from "../hooks/useWebRTC";
import {useParams} from "react-router";
import {useAppSelector} from "../store/hooks/redux";


const Room: FC = () => {

    const {id: roomID} = useParams();
    const {clients, provideMediaRef} = useWebRTC(roomID);
    const {rooms} = useAppSelector(state => state.roomsReducer)

    console.log(rooms)

    return (
        <React.Fragment>
            <Container
                sx={{
                    height: '100vh',
                }}
            >
                <Paper
                    sx={{padding: 2}}
                >
                    <Grid container
                          direction="row"
                          spacing={2}
                          sx={{
                              justifyContent: 'center',
                              verticalAlign: 'middle',
                          }}

                    >
                        {clients.map((clientID: any, index: any) => {
                            return (
                                index < 4 &&

                                <Grid item xs={6} sm={6} md={4} lg={3} xl={2}
                                      sx={{
                                          display: 'flex',
                                          justifyContent: 'center',
                                      }}
                                      key={clientID}
                                >

                                    <div id={clientID} style={{
                                        width: '250px',
                                        height: '250px',
                                        borderRadius: '50%',
                                        overflow: 'hidden',
                                    }}>
                                        <video
                                            style={{
                                                width: '100%',
                                                height: '100%',
                                                lineHeight: 'normal',
                                                display: 'inline-block',
                                                verticalAlign: 'middle',
                                                objectFit: 'cover',
                                            }}
                                            ref={instance => {
                                                provideMediaRef(clientID, instance);
                                            }}
                                            autoPlay
                                            playsInline
                                            muted={clientID === LOCAL_VIDEO}
                                        />
                                    </div>

                                </Grid>
                            );
                        })}


                        {/*<Grid item xs={12} sm={12} md={12} lg={12} xl={12}>*/}
                        {/*    <Paper*/}
                        {/*        sx={{*/}
                        {/*            height: '260px'*/}
                        {/*        }}*/}
                        {/*    >*/}
                        {/*        sa*/}
                        {/*    </Paper>*/}
                        {/*</Grid>*/}

                        {clients.map((clientID: any, index: any) => {
                            return (
                                index > 4 &&

                                <Grid item xs={6} sm={6} md={4} lg={3} xl={2}
                                      sx={{
                                          display: 'flex',
                                          justifyContent: 'center',
                                      }}
                                      key={clientID}
                                >

                                    <div id={clientID} style={{
                                        width: '250px',
                                        height: '250px',
                                        borderRadius: '50%',
                                        overflow: 'hidden',
                                    }}>
                                        <video
                                            style={{
                                                width: '100%',
                                                height: '100%',
                                                lineHeight: 'normal',
                                                display: 'inline-block',
                                                verticalAlign: 'middle',
                                                objectFit: 'cover',
                                            }}
                                            ref={instance => {
                                                provideMediaRef(clientID, instance);
                                            }}
                                            autoPlay
                                            playsInline
                                            muted={clientID === LOCAL_VIDEO}
                                        />
                                    </div>

                                </Grid>
                            );
                        })}

                    </Grid>
                </Paper>
            </Container>
        </React.Fragment>
    )

}

export default Room


