import React, {FC, useEffect, useState} from "react";
import {
    Avatar, Button,
    Container, Divider,
    Grid,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
    Stack,
    Typography
} from "@mui/material";
import {useNavigate} from "react-router";
import {useAppSelector} from "../../store/hooks/redux";
//@ts-ignore
import johnsonImg from "../../static/images/avatar_group.jpg";
import AddRoom from "../AddRoom";
import CurrencyRupeeIcon from '@mui/icons-material/CurrencyRupee';


const UserProfile: FC = () => {

    const navigate = useNavigate()

    let id = (new URL(window.location.href)).pathname.slice(9, (new URL(window.location.href)).pathname.length)

    const {users} = useAppSelector(state => state.usersReducer)
    const {rooms} = useAppSelector(state => state.roomsReducer)
    console.log(rooms)
    const user = users.find(opt => opt.uuid === id)

    const [passRoom, setPassRoom] = useState<string | undefined>(undefined)

    return (
        <React.Fragment>
            <Container>
                <Paper
                    sx={{padding: 2, height: '100hv'}}
                >
                    <Grid container>
                        <Grid
                            xs={6} sm={6} md={6} lg={6} xl={6}
                            item
                            justifyContent="flex-end"
                        >
                            <Typography
                                variant="h6"
                                pt={1}
                                onClick={() => {
                                    navigate('/ccoin-shop/')
                                }}
                            >
                                CCoin {user?.cCoin}
                                <CurrencyRupeeIcon style={{verticalAlign: 'text-top'}}/>
                            </Typography>
                        </Grid>
                        <Grid
                            item
                            xs={6} sm={6} md={6} lg={6} xl={6}>

                            <Stack
                                direction="row"
                                justifyContent="flex-end"
                                spacing={2}
                            >

                                <Typography>{user?.surname} {user?.name}<br/>{user?.patronymic}</Typography>
                                <Avatar
                                    alt="Remy Sharp"
                                    src={user?.photo}
                                    sx={{width: 56, height: 56}}
                                />
                            </Stack>
                        </Grid>
                    </Grid>
                </Paper>
            </Container>

            <Container>
                <Grid container mt={4}>
                    <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                        <Paper sx={{padding: 2}}>
                            <Typography variant="h4" textAlign={'center'}>
                                 
                            </Typography>

                            {
                                rooms !== undefined &&
                                <List sx={{width: '100%', maxWidth: '70%', bgcolor: 'background.paper', marginTop: 4}}>
                                    {rooms.map((opt) => {
                                        return (
                                            <React.Fragment key={opt.uuid}>
                                                <ListItem alignItems="flex-start">
                                                    <ListItemAvatar>
                                                        <Avatar alt="Remy Sharp" src={johnsonImg}/>
                                                    </ListItemAvatar>
                                                    <ListItemText
                                                        primary={opt.title}
                                                        secondary={
                                                            <React.Fragment>
                                                                {
                                                                    opt.users.map((user, index) => {
                                                                        return (
                                                                            <Typography
                                                                                sx={{display: 'inline'}}
                                                                                component="span"
                                                                                variant="body2"
                                                                                color="text.primary"
                                                                                key={index}
                                                                            >
                                                                                {`${user?.name} ${user?.surname} `}
                                                                            </Typography>
                                                                        )
                                                                    })
                                                                }
                                                            </React.Fragment>
                                                        }
                                                    />

                                                    <Button
                                                        style={{
                                                            marginTop: '16px'
                                                        }}
                                                        variant="outlined"
                                                        onClick={() => {
                                                            navigate(`/room/${opt.uuid}`)
                                                        }}
                                                    >
                                                        
                                                    </Button>
                                                </ListItem>
                                                <Divider variant="inset" component="li"/>
                                            </React.Fragment>
                                        )
                                    })}
                                </List>
                            }

                        </Paper>
                    </Grid>
                </Grid>
                <AddRoom/>
            </Container>
        </React.Fragment>
    )
}

export default UserProfile