import {createSlice} from "@reduxjs/toolkit";

export interface IUser {
    surname: string
    name: string
    patronymic: string
    specialtyCode: string
    post: string
    uuid: string
    idTg: string
    photo: string
    cCoin: number
    auth: boolean
    room: string[]
}

interface IInitialState {
    users: IUser[],
    selectUser: IUser | undefined
}

const listUsers: IUser[] = [
    {
        surname: '',
        name: '',
        patronymic: '',
        post: '   ',
        specialtyCode: '',
        uuid: '2',
        idTg: '',
        photo: '',
        cCoin: 2761,
        auth: false,
        room: [],
    },
    {
        surname: '',
        name: '',
        patronymic: '',
        post: '  ',
        specialtyCode: 'Jesus',
        uuid: '1',
        idTg: '',
        photo: '',
        cCoin: 99999,
        auth: false,
        room: [],
    },
]

const initialState: IInitialState = {
    users: listUsers,
    selectUser: undefined
}

export const usersSlice = createSlice({
    name: 'users',
    initialState,
    reducers: {
        select(state, action) {
            state.selectUser = action.payload
        }
    }
})

export default usersSlice.reducer