import {TypedUseSelectorHook, useDispatch, useSelector} from "react-redux";
import {AppDispatch, RootState} from "../store";

export const useAppDispatch = () => useDispatch<AppDispatch>() //  Dispatch  
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector //  Selector