import React, {FC, useState} from "react";
import {
    Autocomplete,
    Box, Button, Checkbox, Dialog, DialogActions,
    DialogContent,
    DialogTitle,
    FormControlLabel,
    SpeedDial,
    SpeedDialIcon,
    Stack,
    TextField
} from "@mui/material";
import {useAppDispatch, useAppSelector} from "../store/hooks/redux";
import {IRoom, roomsSlice} from "../store/reducers/RoomSlice";
import {v4} from "uuid";
import {ClearStore} from "../global-elements/functions/ClearStore";
import {IUser} from "../store/reducers/UsersSlice";


const AddRoom: FC = () => {

    const dispatch = useAppDispatch()

    const [open, setOpen] = React.useState(false);
    const {users, selectUser} = useAppSelector(state => state.usersReducer)
    const {rooms} = useAppSelector(state => state.roomsReducer)

    const [dataRoom, setDataRoom] = useState<IRoom>()

    const handleClose = () => {
        setOpen(false);
    };

    const handleAdd = () => {

        const url = v4()

        const admin: any[] = []

        admin.push(selectUser)

        if (rooms !== undefined) {
            dispatch(ClearStore(roomsSlice.actions.add([
                ...rooms,
                {
                    title: dataRoom?.title,
                    // @ts-ignore
                    userLimit: dataRoom?.userLimit < 1 ? 1 : dataRoom?.userLimit,
                    private: !dataRoom?.private,
                    password: dataRoom?.password,
                    uuid: url,
                    photo: 'src/pages/avatar_group.jpg',
                    admin: admin,
                    users: dataRoom?.users
                }
            ])))
        } else {
            dispatch(ClearStore(roomsSlice.actions.add([
                {
                    title: dataRoom?.title,
                    // @ts-ignore
                    userLimit: dataRoom?.userLimit < 1 ? 1 : dataRoom?.userLimit,
                    private: dataRoom?.private === undefined ? false : dataRoom?.private,
                    password: dataRoom?.password,
                    uuid: url,
                    photo: 'src/pages/avatar_group.jpg',
                    admin: [selectUser],
                    users: [selectUser]
                }
            ])))
        }


        setOpen(false)
    };

    return (
        <React.Fragment>
            <Box sx={{transform: 'translateZ(0px)', flexGrow: 1}}>
                <SpeedDial
                    ariaLabel="SpeedDial basic example"
                    sx={{position: 'absolute', bottom: 16, right: 16}}
                    icon={<SpeedDialIcon/>}
                    onClick={() => setOpen(true)}
                />
            </Box>

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="  "
                aria-describedby=""
            >
                <DialogTitle id="alert-dialog-title">
                    {" "}
                </DialogTitle>
                <DialogContent style={{width: '500px'}}>
                    <Stack
                        direction={'column'}
                        spacing={2}
                    >
                        <TextField id="title" label=" " variant="standard" onChange={(e) => {
                            //@ts-ignore
                            setDataRoom({
                                ...dataRoom,
                                title: e.target.value
                            })
                        }}/>

                        <Autocomplete
                            multiple
                            id="tags-standard"
                            options={users}
                            getOptionLabel={(option) => `${option.surname} ${option.name} ${option.patronymic}`}
                            //@ts-ignore
                            defaultValue={[]}
                            //@ts-ignore
                            onChange={(event: any, newValue: string | null) => {
                                setDataRoom({
                                    ...dataRoom,
                                    //@ts-ignore
                                    users: newValue
                                })
                            }}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="standard"
                                    label=""
                                    placeholder=" "
                                />
                            )}
                        />

                        <FormControlLabel control={<Checkbox
                            onClick={() => {
                                //@ts-ignore
                                setDataRoom({
                                    ...dataRoom,
                                    private: !dataRoom?.private
                                })
                            }}
                        />} label=" "/>
                    </Stack>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}></Button>
                    <Button onClick={handleAdd} autoFocus>
                        
                    </Button>
                </DialogActions>
            </Dialog>

        </React.Fragment>
    )
}

export default AddRoom