import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import React, {FC} from "react";

//@ts-ignore
import banka from '../static/images/banka.jpg'
//@ts-ignore
import futbolka from '../static/images/futbolka.jpg'
//@ts-ignore
import plashandusi from '../static/images/plashandusi.jpg'
//@ts-ignore
import nft from '../static/images/nft.jpg'
import {Container, Grid, Paper, Stack} from "@mui/material";
import CurrencyRupeeIcon from "@mui/icons-material/CurrencyRupee";
import {useAppSelector} from "../store/hooks/redux";

const Shop: FC = () => {

    const {selectUser} = useAppSelector(state => state.usersReducer)

    return (
        <React.Fragment>

            <Container>
                <Paper
                    sx={{padding: 2, height: '100hv'}}
                >
                    <Grid container>
                        <Grid
                            xs={12} sm={12} md={12} lg={12} xl={12}
                            item
                            style={{textAlign: 'center'}}
                        >
                            <Typography
                                variant="h6"
                                pt={1}
                            >
                                <Typography variant="h4">Conf Coin</Typography>
                                <br/>
                                  {selectUser?.cCoin}
                                <CurrencyRupeeIcon style={{verticalAlign: 'text-top'}}/>
                            </Typography>
                        </Grid>
                    </Grid>
                </Paper>
            </Container>
            <Container>
                <Grid container
                      direction="row"
                      justifyContent="center"
                      alignItems="center"
                      style={{marginTop: '32px', marginBottom: '24px'}}
                >
                    <Grid item xs={12} sm={12} md={12} lg={12} xl={12} style={{
                        display: 'flex',
                        justifyContent: 'center'
                    }}>
                        <Stack direction={'column'} spacing={2}>
                            <Stack direction={'row'} spacing={2}>
                                <Card sx={{width: 345}}>
                                    <CardMedia
                                        component="img"
                                        height="194"
                                        image={banka}
                                        alt=""
                                    />
                                    <CardContent>
                                        <Typography variant="body2" color="text.secondary">
                                            , , , ,  ,  
                                        </Typography>
                                    </CardContent>
                                    <CardActions disableSpacing>
                                        <IconButton aria-label="add to favorites">
                                            <AddShoppingCartIcon/>
                                        </IconButton>
                                    </CardActions>
                                </Card>
                                <Card sx={{width: 345}}>
                                    <CardMedia
                                        component="img"
                                        height="194"
                                        image={futbolka}
                                        alt=""
                                    />
                                    <CardContent>
                                        <Typography variant="body2" color="text.secondary">
                                            doeat(), dosleep(), doeat(), dosleep()
                                        </Typography>
                                    </CardContent>
                                    <CardActions disableSpacing>
                                        <IconButton aria-label="add to favorites">
                                            <AddShoppingCartIcon/>
                                        </IconButton>
                                    </CardActions>
                                </Card>
                            </Stack>
                            <Stack direction={'row'} spacing={2}>
                                <Card sx={{width: 345}}>
                                    <CardMedia
                                        component="img"
                                        height="194"
                                        image={nft}
                                        alt=""
                                    />
                                    <CardContent>
                                        <Typography variant="body2" color="text.secondary">
                                              .
                                        </Typography>
                                    </CardContent>
                                    <CardActions disableSpacing>
                                        <IconButton aria-label="add to favorites">
                                            <AddShoppingCartIcon/>
                                        </IconButton>
                                    </CardActions>
                                </Card>
                                <Card sx={{width: 345}}>
                                    <CardMedia
                                        component="img"
                                        height="194"
                                        image={plashandusi}
                                        alt="  "
                                    />
                                    <CardContent>
                                        <Typography variant="body2" color="text.secondary">
                                            Page not foundddddd (404040440404040)
                                        </Typography>
                                    </CardContent>
                                    <CardActions disableSpacing>
                                        <IconButton aria-label="add to favorites">
                                            <AddShoppingCartIcon/>
                                        </IconButton>
                                    </CardActions>
                                </Card>
                            </Stack>
                        </Stack>


                    </Grid>
                </Grid>
            </Container>
        </React.Fragment>
    )
}

export default Shop